<template>
    <div class="controlDesk">
        <div class="controlDesk_top">
            <div class="controlDesk_top_left">
                <div class="messages_total bg">
                    <div class="title">
                        <span></span>
                        <div class="title_name">监测统计</div>
                    </div>
                    <div class="messages_total_main">
                        <div class="messages">
                            <div class="messages_type_num">{{ devdata.typecount }}</div>
                            <div class="title_color_fs">传感器类型数量</div>
                        </div>
                        <div class="messages">
                            <div class="messages_num">{{ devdata.devicecount }}</div>
                            <div class="title_color_fs">传感器安装数量</div>
                        </div>
                    </div>
                </div>
                <div class="meteorological_total bg">
                    <div class="title">
                        <span></span>
                        <div class="title_name">气象监测</div>
                        <div class="more" @click="tometeorological"> 更多<span
                                class="title_ic_more station-imgpostion"></span>
                        </div>
                    </div>
                    <div class="meteorological">
                        <el-row :span="12">
                            <el-col :span="8">
                                <div class="total-bg">
                                    <div class="meteorological1_img station-imgpostion img"></div>
                                    <div class="test">
                                        <span class="title_color_fs">温度</span>
                                        <span class="number">{{ metdatalist.temp }}℃ </span>
                                    </div>
                                </div>
                            </el-col>
                            <el-col :span="8">
                                <div class="total-bg">
                                    <div class="meteorological2_img station-imgpostion img"></div>
                                    <div class="test">
                                        <span class="title_color_fs">湿度</span>
                                        <span class="number">{{ metdatalist.humidity }}% </span>
                                    </div>
                                </div>
                            </el-col>
                            <el-col :span="8">
                                <div class="total-bg">
                                    <div class="meteorological3_img station-imgpostion img"></div>
                                    <div class="test">
                                        <span class="title_color_fs">雨量</span>
                                        <span class="number">{{ metdatalist.rainfall }}mm</span>
                                    </div>
                                </div>
                            </el-col>
                        </el-row>
                        <el-row :span="12">
                            <el-col :span="8">
                                <div class="total-bg">
                                    <div class="meteorological4_img station-imgpostion img"></div>
                                    <div class="test">
                                        <span class="title_color_fs">气压</span>
                                        <span class="number">{{ metdatalist.pressure }}hPa </span>
                                    </div>
                                </div>
                            </el-col>
                            <el-col :span="8">
                                <div class="total-bg">
                                    <div class="meteorological5_img station-imgpostion img"></div>
                                    <div class="test">
                                        <span class="title_color_fs">风速</span>
                                        <span class="number">{{ metdatalist.windspeed }}m/s </span>
                                    </div>
                                </div>
                            </el-col>
                            <el-col :span="8">
                                <div class="total-bg">
                                    <div class="meteorological6_img station-imgpostion img"></div>
                                    <div class="test">
                                        <span class="title_color_fs">风向</span>
                                        <span class="number">{{ metdatalist.winddirection }}° </span>
                                    </div>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </div>
                <div class="cableDuct_total bg">
                    <div class="title">
                        <span></span>
                        <div class="title_name">电缆沟及油池状态监测</div>
                        <div class="more" @click="tocableDuct"> 更多<span class="title_ic_more station-imgpostion"></span>
                        </div>
                    </div>
                    <div class="cableDuct">
                        <el-row :span="8">
                            <el-col :span="12">
                                <div class="cableDuct_main" v-if="waterlist.smoke_state !== ''">
                                    <div class="test">
                                        <span class="title_color_fs">烟感状态</span>
                                        <span v-show="waterlist.smoke_state == 0" :class="[
                                            waterlist.smoke_state == 0
                                                ? 'number'
                                                : 'otherActiveClass'
                                        ]">其他 </span>
                                        <span v-show="waterlist.smoke_state == 2" :class="[
                                            waterlist.smoke_state == 2
                                                ? 'number'
                                                : 'otherActiveClass'
                                        ]">正常</span>
                                        <span v-show="waterlist.smoke_state == 1" :class="[
                                            waterlist.smoke_state == 1
                                                ? 'otherActiveClass'
                                                : 'number'
                                        ]">有烟 </span>
                                    </div>
                                    <div class="meteorological7_img station-imgpostion img2" :class="[
                                        waterlist.smoke_state != 1
                                            ? 'meteorological7_img'
                                            : 'meteorological7_img1'
                                    ]"></div>
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div class="cableDuct_main" v-if="waterlist.oilpool_level">
                                    <div class="test" v-if="waterlist.oilpool_level">
                                        <span class="title_color_fs">油池液位</span>
                                        <span :class="[
                                            waterlist.oilpool_level < 3500
                                                ? 'number'
                                                : 'otherActiveClass'
                                        ]">{{ waterlist.oilpool_level }}mm</span>
                                    </div>
                                    <div v-if="waterlist.oilpool_level" class="meteorological8_img station-imgpostion img2"
                                        :class="[
                                            waterlist.oilpool_level < 3500
                                                ? 'meteorological8_img'
                                                : 'meteorological8_img1'
                                        ]"></div>
                                </div>
                            </el-col>
                        </el-row>
                        <el-row :span="8">
                            <el-col :span="12">
                                <div class="cableDuct_main" v-if="waterlist.waterlevel">
                                    <div class="test" v-if="waterlist.waterlevel">
                                        <span class="title_color_fs">电缆沟水位</span>
                                        <span :class="[
                                            waterlist.waterlevel < 3000
                                                ? 'number'
                                                : 'otherActiveClass'
                                        ]">{{ waterlist.waterlevel }}m </span>
                                    </div>
                                    <div v-if="waterlist.waterlevel" class="meteorological9_img station-imgpostion img2"
                                        :class="[
                                            waterlist.waterlevel < 3000
                                                ? 'meteorological9_img'
                                                : 'meteorological9_img1'
                                        ]"></div>
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div class="cableDuct_main">
                                    <div class="test">
                                        <span class="title_color_fs">水浸状态</span>
                                        <span v-if="waterlist.waterimmersionms_state != 1" :class="[
                                            waterlist.waterimmersionms_state == 2
                                                ? 'number'
                                                : 'otherActiveClass'
                                        ]">无水 </span>
                                        <span v-else :class="[
                                            waterlist.waterimmersionms_state == 1
                                                ? 'number'
                                                : 'otherActiveClass'
                                        ]">有水 </span>
                                    </div>
                                    <div class="meteorological0_img station-imgpostion img2" :class="[
                                        waterlist.waterimmersionms_state != 1
                                            ? 'meteorological0_img'
                                            : 'meteorological0_img1'
                                    ]"></div>
                                </div>
                            </el-col>
                        </el-row>
                        <el-row :span="8">
                            <el-col :span="12">
                                <div class="cableDuct_main" v-if="waterlist.reservoirthickness">
                                    <div class="test" v-if="waterlist.reservoirthickness">
                                        <span class="title_color_fs">油池油污</span>
                                        <span :class="[
                                            waterlist.reservoirthickness < 3000
                                                ? 'number'
                                                : 'otherActiveClass'
                                        ]">{{ waterlist.reservoirthickness }}mm </span>
                                    </div>
                                    <div v-if="waterlist.reservoirthickness"
                                        class="meteorological11_img station-imgpostion img2" :class="[
                                            waterlist.reservoirthickness < 3000
                                                ? 'meteorological11_img'
                                                : 'meteorological11_img1'
                                        ]"></div>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </div>
            </div>
            <div class="controlDesk_top_right">
                <div class="vibration_total bg">
                    <div class="title">
                        <span></span>
                        <div class="title_name">换流变振动监测</div>
                        <div class="more" @click="tovibration"> 更多<span class="title_ic_more station-imgpostion"></span>
                        </div>
                    </div>
                    <div class="echarts">
                        <el-carousel :interval="5000" arrow="always" style="width: 1130px;height: 240px;">
                            <el-carousel-item v-for="(item, i) in dat1" :key="i">
                                <div style="display: flex">
                                    <div class="vibration_chicos">
                                        <span>{{ item.device_name }}</span>
                                        <span class="title_color_fs">最大振动值</span>
                                        <span>{{ item.maxvalue }}m/s²</span>
                                    </div>
                                    <vibrationEcharts :dat1="item.chartlist" :name="item.device_name" :id="i" v-if="dat1" />
                                </div>
                            </el-carousel-item>
                        </el-carousel>
                    </div>
                </div>
                <div class="SwitchMachine_total bg">
                    <div class="title">
                        <span></span>
                        <div class="title_name">分接开关机械特性监测</div>
                        <div class="more" @click="toSwitchMachine"> 更多<span class="title_ic_more station-imgpostion"></span>
                        </div>
                    </div>
                    <div class="SwitchMachine" style="height: 240px;">
                        <el-carousel :interval="5000" arrow="always" style="width: 1130px;height: 255px;">
                            <el-carousel-item v-for="(item, i) in sedata" :key="i">
                                <div class="SwitchMachine_type">
                                    <div class="data1 date">
                                        <span>{{ item.lasttime }}</span>
                                        <span class="title_color_fs">最近一次动作时间</span>
                                    </div>
                                    <div class="data1 date">
                                        <span>{{ item.duration }}</span>
                                        <span class="title_color_fs">持续时间</span>
                                    </div>
                                    <div class="data1 date">
                                        <span>{{ item.avg_current }}</span>
                                        <span class="title_color_fs">平均电流</span>
                                    </div>
                                    <div class="data1 date">
                                        <span>{{ item.max_current }}</span>
                                        <span class="title_color_fs">最大电流</span>
                                    </div>
                                </div>
                                <div class="SwitchMachine_echarts">
                                    <SwitchMachineEcharts :sedata="item.chartlist" :name="item.device_name" :id="i"
                                        v-if="sedata" />
                                </div>
                            </el-carousel-item>
                        </el-carousel>
                    </div>
                </div>
            </div>
        </div>
        <div class="controlDesk_buttom bg">
            <div class="alarmInformation_total">
                <div class="title">
                    <span></span>
                    <div class="title_name">报警列表</div>
                    <div class="timeChioc" @click="nowday">今日</div>
                    <div class="timeChioc" @click="tenday">最近10天</div>
                    <div class="timeChioc" @click="therrhday">最近30天</div>
                    <div class="more" @click="toalarmInformation"> 更多<span class="title_ic_more station-imgpostion"></span>
                    </div>
                </div>
                <div class="alarmInformation_echarts">
                    <alarmecharts :datalist="datalist" v-if="datalist" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import controlDesk from "./controlDesk";
export default controlDesk;
</script>
<style scoped>
@import url("controlDesk.css");
@import url("../../assets/css/common.css");
</style>



