import vibrationEcharts from "./vibrationEcharts.vue";
import bxEcharts from "./bxEcharts.vue";
import SwitchMachineEcharts from "./SwitchMachineEcharts.vue";
import alarmecharts from "./alarmecharts.vue";
import { Col } from "iview";
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import { locale, loadMessages } from "devextreme/localization";
import {
  devicestatistics,
  meteorological,
  oilpool_state,
  alarm_chart,
  switch_vibration,
  vibration_chart,
  switch_chart
} from "../../utils/service";
import mqtt from "mqtt";
// import {
//   mqttConfig
// } from "../../commonParams/index.js"

export default {
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
    this.initMqtt();
    // this.getdata()
  },
  components: {
    vibrationEcharts,
    bxEcharts,
    SwitchMachineEcharts,
    alarmecharts,
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling
  },
  data() {
    return {
      isActive: 0,
      pageSizes: [10, 20, 100],
      displayMode: "full",
      showPageSizeSelector: true,
      showInfo: true,
      showNavButtons: true,
      devdata: "",
      metdatalist: "",
      waterlist: {},
      datalist: "",
      edata: "",
      vibrate_chart: "",
      evibrate_chart: "",
      switch_vibration: "",
      eswitch_vibration: "",
      sedata: "",
      showdata: "",
      dat1: ""
    };
  },
  watch: {
    waterlist(newValue, oldValue) {
      // this.initMqtt();
    }
  },
  mounted() {
    this.getdata();
    // WorkMqtt.Connection()
    this.therrhday();
    this.get_switch_vibration();
    this.get_vibrate_chart();
    this.initMqtt();
  },
  methods: {
    initMqtt(topic) {
      const mqttConfig = {
        port: 8083,
        keepalive: 60,
        clientId:
          "mqttjs_" +
          Math.random()
            .toString(16)
            .substr(2, 8)
        // protocolId: 'MQTT',
        // protocolVersion: 4,
        // clean: true,
      };
      var TopicList = {
        meteorological: "topic/mqtt/meteorological",
        devicestatistics: "topic/mqtt/devicestatistics",
        oilpoolstate: "topic/mqtt/oilpoolstate",
        smoke_check: "topic/mqtt/smoke_check"
      };
      const _this = this;
      //建立连接，需要搭建消息中间件服务器的（我这里是后台）提供用户名或密码
      const cfg = Object.assign(mqttConfig, {
        // username: 'xxxx1',
        // password: 'xxx2',
      });
      // 在data中定义MQTT,以便断开连接
      // ws://xxxxx是连接地址，后台提供，wss是https连接
      _this.MQTT = mqtt.connect("ws://8.142.173.95:8083/mqtt", cfg);
      _this.MQTT.on("error", e => {
        _this.MQTT.end();
      });
      //建立连接后订阅主题
      _this.MQTT.on("connect", () => {
        // 订阅一个主题
        for (let k in TopicList) {
          const element = TopicList[k];
          _this.MQTT.subscribe(
            element,
            {
              qos: 1
            },
            err => {
              if (!err) {
                // console.info(' ---- 订阅成功')
              } else {
                // console.warn('订阅失败')
              }
            }
          );
        }
      });
      // 后台发送的消息
      _this.MQTT.on("message", function(top, message) {
        //发送过来的如果是对象你是需要解析的。
        const res = JSON.parse(message.toString());
        const TOP = top;
        // console.log(top + ': ', res);
        //根据不同主题进行赋值操作
        switch (TOP) {
          case "topic/mqtt/meteorological":
            var slist = res.data[0];
            _this.$nextTick(() => {
              _this.metdatalist.temp = slist.temp;
              _this.metdatalist.humidity = slist.humidity;
              _this.metdatalist.rainfall = slist.rainfall;
              _this.metdatalist.pressure = slist.pressure;
              _this.metdatalist.windspeed = slist.windspeed;
              _this.metdatalist.winddirection = slist.winddirection;
            });
            break;
          case "topic/mqtt/devicestatistics":
            break;
          //其他情况
          case "topic/mqtt/oilpoolstate":
            // console.log('top :>> ', TOP);
            var slist = res.data;
            _this.$nextTick(() => {
              _this.waterlist.smoke_state = slist.smoke_state;
              _this.waterlist.oilpool_level = slist.oilpool_level;
              _this.waterlist.waterimmersionms_state =
                slist.waterimmersionms_state;
              _this.waterlist.waterlevel = slist.waterlevel;
              _this.waterlist.reservoirthickness = slist.reservoirthickness;
            });
            break;
          case "topic/mqtt/smoke_check":
            // var slist = res.data
            // _this.$nextTick(() => {
            //   var smoke_state = slist.findIndex(item => item.smokeds == 2)
            //   _this.waterlist.smoke_state = smoke_state
            // })
            break;
        }
      });
      // 其他异常事件
      _this.MQTT.on("reconnect", () => {
        // console.info('正在重连')
      });
      _this.MQTT.on("disconnect", error => {
        // console.info('服务器断开:', error)
      });
      _this.MQTT.on("close", () => {
        _this.MQTT.end();
      });
    },
    get_switch_vibration() {
      switch_chart({}, res => {
        var list = res.data;
        list.map(item => {
          var listpush = [];
          item.chart_data.map(x => {
            listpush.push([x.acquisitiontime, x.maxcura]);
          });
          item.chartlist = listpush;
        });
        this.sedata = list;
      });
    },
    get_vibrate_chart() {
      vibration_chart({}, res => {
        var list = res.data;
        list.map(item => {
          var listpush = [];
          item.chart_data.map(x => {
            listpush.push([x.acquisitiontime, x.accx]);
          });
          item.chartlist = listpush;
        });
        this.dat1 = list;
      });
    },
    getdata() {
      devicestatistics({}, res => {
        // console.log(res);
        this.devdata = res.data[0] ? res.data[0] : {};
      });
      meteorological({}, res => {
        this.metdatalist = res.data[0];
      });
      oilpool_state({}, res => {
        this.waterlist = res.data;
      });
    },
    nowday() {
      this.datalist = [];
      var data = {
        days: "0"
      };
      alarm_chart(data, res => {
        var datalist = res.data;
        var listpush = [];
        if (datalist == null) {
          this.$message.error("当前无报警数据");
          return "";
        } else {
          datalist.map(item => {
            listpush.push([item.date, item.total]);
          });
        }
        this.datalist = listpush;
      });
    },
    tenday() {
      this.datalist = [];
      var data = {
        days: "10"
      };
      alarm_chart(data, res => {
        var datalist = res.data;
        var listpush = [];
        if (datalist == null) {
          this.$message.error("当前无报警数据");
          return "";
        } else {
          datalist.map(item => {
            listpush.push([item.date, item.total]);
          });
        }
        this.datalist = listpush;
      });
    },
    therrhday() {
      this.datalist = [];
      var data = {
        days: "30"
      };
      alarm_chart(data, res => {
        var datalist = res.data;
        var listpush = [];
        if (datalist == null) {
          this.$message.error("当前无报警数据");
          return "";
        } else {
          datalist.map(item => {
            listpush.push([item.date, item.total]);
          });
        }
        this.datalist = listpush;
      });
    },
    tometeorological() {
      this.$router.replace("/meteorological");
    },
    tocableDuct() {
      this.$router.replace("/cableDuct");
    },
    tovibration() {
      this.$router.replace("/vibration");
    },
    toSwitchMachine() {
      this.$router.replace("/SwitchMachine");
    },
    toalarmInformation() {
      this.$router.replace("/alarmInformation");
    }
  },

  beforeDestroy() {
    if (this.MQTT.end) this.MQTT.end();
  }
};



